<template>
  <div class="search">
    <van-nav-bar title="商品搜索" left-arrow @click-left="$router.push('/home')" />

    <van-search v-model="search" show-action placeholder="请输入搜索关键词" clearable>
      <template #action>
        <div @click="GoSearch(search)">搜索</div>
      </template>
    </van-search>

    <!-- 搜索历史 -->
    <div class="search-history" v-if="history.length>0">
      <div class="title">
        <span>最近搜索</span>
        <van-icon name="delete-o" size="16"  @click="clear()"/>
      </div>
      <div class="list">
        <div v-for="item in history" :key="item" class="list-item" @click="GoSearch(item)">{{item}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { getHistoryList, setHistoryList } from '@/utils/storage'
import { Toast } from 'vant'
export default {
  name: 'SearchIndex',
  data () {
    return {
      search: '',
      history: getHistoryList()
      // ['手机', '电脑', '电视', '空调', '冰箱']
    }
  },
  methods: {
    // 最近搜索
    GoSearch (e) {
      if (e === '') {
        return Toast('请输入搜索关键词')
      }
      const index = this.history.indexOf(e)
      if (index !== -1) {
        // 查找当前是否存在，存在则移动至前方
        this.history.splice(index, 1)
      }
      this.history.unshift(e)
      this.search = ''
      setHistoryList(this.history)
      this.$router.push(`/searchlist?search=${e}`)
    },
    // 清空
    clear () {
      this.history = []
      setHistoryList(this.history)
    }
  }
}
</script>

<style lang="less" scoped>
.search {
  .searchBtn {
    background-color: #fa2209;
    color: #fff;
  }
  ::v-deep .van-search__action {
    background-color: #c21401;
    color: #fff;
    padding: 0 20px;
    border-radius: 0 5px 5px 0;
    margin-right: 10px;
  }
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  .title {
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
  }
  .list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    padding: 0 10px;
    gap: 5%;
  }
  .list-item {
    width: 30%;
    text-align: center;
    padding: 7px;
    line-height: 15px;
    border-radius: 50px;
    background: #fff;
    font-size: 13px;
    border: 1px solid #efefef;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-bottom: 10px;
  }
}
</style>
